:root {
  --color-main: #308ca9;
  --color-main2: rgb(0, 185, 185);
  --color-main3: rgb(53, 168, 168);

  --color-container: hsl(187, 63%, 34%);
  --color-container2: #20605d;
  --color-container3: #3dd2ff;

  --color-green: #36f936;
  --color-red: rgb(233, 233, 233);
  --color-yellow: yellow;
  --color-yellow2: rgb(255, 255, 160);
  --med: 10px 10px;
}

* {
  padding: 0;
}

body {
  background-color:rgb(0, 0, 0);
  overflow-x: hidden;
}

.container {
  margin: 2rem auto;
  display: flex;
  flex-wrap: wrap;
}
.anime {
  margin: 1rem auto;
  width: 20rem;
  height: 26rem;
  background-color:rgb(24, 24, 24);
  border-radius: 10px;
  border: 1px solid rgb(201, 48, 2);
  display: flex;
  flex-direction: column;
  padding: 10px;
}
.green,
.red,
.yellow {
  font-weight: 900;
}
.green {
  color: var(--color-green);
}
.red {
  color: rgb(255, 23, 23);
}
.yellow {
  color: var(--color-yellow);
}
h2,
h3,
p,
li {
  color: rgb(218, 218, 218);
  font-family: Roboto;
}

h1 {
  font-size: 1.3rem;
  text-align: center;
  text-transform: uppercase;
  font-weight: 900;
  font-family: Roboto, Ubuntu;
  color: var(--color-yellow);
}
.img {
  object-fit:cover;
  height: 20em;
  width: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.anime-bg{
  object-fit:cover;
  margin: 1rem auto;
  height:40rem;
  width: 100%;
}
.a,.b
.img {
  cursor: pointer;
}
input,
.input-container {
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 35%;
  border-radius: 10px;
  height: 2rem;
}
input{
  width: 100%;
}
.a,
.b{
  height: 2rem;
  width: 100%;
  font-weight: 1000;
  text-align: center;
  border: solid 1px;
}
.input-container{
padding:.8rem;
background-color: rgb(242, 255, 0);
}
.a {
  background-color: var(--color-yellow);
}
.b{
  background-color: var(--color-yellow);
}
button:hover {
  background-color: var(--color-yellow2);
  transition: 1s;
  color: var(--color-main);
}
.answer {
  color: #fff;
  padding: 1rem;
  max-height: 0;
  overflow: hidden;
  transition: all 0.5s cubic-bezier(0, 1, 0, 1);
}
.answer.show {
  height: auto;
  max-height: 99999px;
  transition: all 1s cubic-bezier(1, 0, 1, 0);
}

@media screen and (max-width: 800px) {
  .anime {
    flex-wrap: wrap;
    width: 70vw;
    align-items: center;
    overflow: hidden;
    justify-content: center;
    height: auto;
  }
}
